.header {
  width: 100%;
  position: relative;
}

.navbar {
  background: transparent;
  backdrop-filter: blur(6px);
  padding: 0.75rem 1.25rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo a {
  color: var(--text-color);
  font-size: 1.15rem;
  font-weight: 700;
  text-decoration: none;
  letter-spacing: 0.3px;
}

.navbar ul {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
  gap: 1rem;
}

.navbar li {
  margin-left: 1.5rem;
}

.navbar a {
  color: var(--text-color);
  text-decoration: none;
  font-size: 0.98rem;
  transition: color 0.15s ease, transform 0.15s ease;
  padding: 0.35rem 0.5rem;
  border-radius: 6px;
}

.navbar a:hover { color: var(--primary-color); transform: translateY(-2px); }

.light-mode-switch {
  position: relative; 
  top: auto;
  right: auto;
  min-width: 40px;
  height: 34px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.15s ease;
  z-index: 1001;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  background: transparent;
  color: var(--text-color);
  border: 1px solid var(--border-color);
  padding: 6px 8px;
}

.light-mode-switch.light {
    background-color: var(--secondary-color);
    border: 2px solid var(--light-text-color);
}

.light-mode-switch.dark {
    background-color: var(--light-text-color);
    border: 2px solid var(--secondary-color);
}

.banner {
  padding: 6.5rem 2rem 4.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.banner-content {
  display: flex;
  max-width: 1200px;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.text-content {
  flex: 1;
  padding-right: 2rem;
}

/* spacing for embedded badges (TryHackMe iframe) to sit lower in the hero */
.text-content .badges {
  margin-top: 18px;
}

.text-content h1, .text-content h2, .text-content h3 { color: var(--text-color); }

/* Eyebrow / small lead-in */
.text-content h3 {
  font-size: 0.9rem;
  margin: 0 0 10px;
  color: var(--muted-color);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.8px;
  opacity: 0.95;
}

/* Main heading with clipped gradient for a modern look */
.text-content h1 {
  font-size: clamp(2.6rem, 6vw, 3.6rem);
  margin: 0 0 10px;
  line-height: 1.02;
  font-weight: 800;
  background: linear-gradient(90deg, var(--primary-color), var(--accent-color));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent; /* shows the gradient */
  display: block; /* ensure name is on its own line */
}

/* Subtitle / role â€” subtle, slightly muted */
.text-content h2 {
  font-size: 1.05rem;
  margin: 8px 0 0;
  font-weight: 600;
  color: var(--muted-color);
  display: block; /* place subtitle on its own line */
}

/* small accent bar after the subtitle */
.text-content h2::after {
  content: '';
  display: inline-block;
  width: 44px;
  height: 4px;
  margin-left: 12px;
  vertical-align: middle;
  border-radius: 2px;
  background: linear-gradient(90deg, var(--primary-color), var(--accent-color));
}

/* Glitch heading (lightweight) */
.glitch {
  position: relative;
  display: block; /* ensure headline is on its own line */
  margin-bottom: 6px;
}
.glitch::before,
.glitch::after {
  content: attr(data-text);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  pointer-events: none;
  opacity: 0.8;
}
.glitch::before {
  color: var(--primary-color);
  transform: translate(-2px, -1px);
  clip-path: inset(0 0 60% 0);
}
.glitch::after {
  color: var(--accent-color);
  transform: translate(2px, 1px);
  clip-path: inset(60% 0 0 0);
}

/* Typewriter subtitle (simple, no JS) */
.typewriter {
  display: block; /* place subtitle on its own line */
  overflow: hidden;
  white-space: nowrap;
  max-width: 100%;
}

/* Profile badge overlay */
.profile-image-container { position: relative; display: inline-block; }
.profile-image-container img { border-radius: 50%; display: block; }
.security-badge {
  position: absolute;
  right: -6px;
  bottom: -6px;
  background: linear-gradient(90deg, var(--primary-color), var(--accent-color));
  color: var(--light-text-color);
  padding: 8px 10px;
  border-radius: 10px;
  box-shadow: var(--box-shadow);
  font-size: 14px;
}

/* Scroll indicator */
.scroll-indicator { display:flex; flex-direction:column; align-items:center; gap:8px; margin: 12px 0 0; color:var(--muted-color); }
.scroll-indicator .mouse { width:28px; height:44px; border-radius:20px; border:2px solid var(--muted-color); display:inline-flex; align-items:flex-start; justify-content:center; padding-top:6px; }
.scroll-indicator .wheel { width:6px; height:6px; background:var(--muted-color); border-radius:50%; display:block; animation: scroll-wheel 1.6s infinite; }
@keyframes scroll-wheel { 0% { transform: translateY(0); opacity:1 } 50% { transform: translateY(8px); opacity:0.6 } 100% { transform: translateY(0); opacity:1 } }

/* Respect reduced motion */
@media (prefers-reduced-motion: reduce) {
  .scroll-indicator .wheel { animation: none; }
  .glitch::before, .glitch::after { opacity: 0; }
}

.profile-photo {
  flex: 1;
  display: flex;
  justify-content: center;
}

.profile-photo img {
  width: 220px;
  height: 220px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid color-mix(in srgb, var(--background-color) 80%, transparent);
  box-shadow: 0 8px 24px rgba(2,6,23,0.06);
}

.menu-icon {
  display: none;
  background: transparent;
  border: none;
  padding: 6px;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  gap: 4px;
}

@media (max-width: 768px) {
  .navbar ul {
    display: none;
  }

  .navbar ul.open {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 100%;
    right: 0;
    left: auto;
    padding: 1rem;
    z-index: 1001;
    /* use theme variable for consistent background in dark mode */
    background-color: color-mix(in srgb, var(--background-color) 90%, black 6%);
    width: auto;
    margin-right: 0;
  }

  .navbar li {
    margin: 0.5rem 0;
    text-align: left;
  }

  .navbar li a {
    display: block;
    padding: 0.5rem 1rem;
    color: var(--light-text-color);
    text-decoration: none;
    font-size: 1.1rem;
  }

  .menu-icon {
    display: block;
    cursor: pointer;
  }

  .menu-icon span {
    display: block;
    width: 25px;
    height: 3px;
    background-color: var(--light-text-color);
    margin: 0;
    border-radius: 2px;
  }

  .banner-content {
    flex-direction: column-reverse;
    text-align: center;
    gap: 18px; /* add spacing between stacked hero elements on mobile */
  }

  .text-content {
    padding-right: 0;
    margin-top: 2rem;
  }

  .text-content .badges { margin-top: 12px; }

  .profile-photo img {
    width: 200px;
    height: 200px;
  }

  .text-content h1 {
    font-size: 3rem;
  }

  .text-content h2 {
    font-size: 2rem;
  }

  .text-content h3 {
    font-size: 1.5rem;
  }
}

@media (max-width: 375px) {
  .profile-photo img { width: 160px; height: 160px; }
  .banner { padding: 4.5rem 1rem 3rem; }
}
